import React from "react"
import { Modal, Heading } from "components"
import { Row, Col, Typography, Avatar, Icon } from "antd"
import { pricer, mobile } from "helpers"
import { Alert } from "components"
import styled from "styled-components"
import { Button } from "components"
import { Trans } from "@lingui/macro"

const HorizontalRow = styled(Row).attrs(() => ({
	type: "flex",
	gutter: 16,
}))`
	&& {
		height: auto;
		margin-bottom: 1.5em;
		padding-top: 1em;
		flex-wrap: nowrap;
		overflow-x: scroll;
		width: auto;
		scroll-snap-type: x mandatory;
		-webkit-overflow-scrolling: touch;

		.ant-col {
			scroll-snap-align: center;
			scroll-snap-stop: always;
		}

		&::-webkit-scrollbar {
			display: none;
		}
	}
`

export default function ModalDetail({ data = {}, ...props }) {
	const price = pricer(data.prices)
	const listing = data.siteListings || {}

	return (
		<Modal {...props} width={800} footer={null}>
			<Row gutter={16}>
				<Col lg={10}>
					<HorizontalRow>
						{(data.images || []).map((item, idx) => (
							<Col lg={24} xs={24} key={item.title + idx} className="ta-center">
								<img src={item.file.url} alt={item.title} width="100%" />
								<Typography.Text type="secondary">{item.title}</Typography.Text>
							</Col>
						))}
					</HorizontalRow>
				</Col>
				<Col lg={14}>
					<Heading
						level={4}
						content={data.title}
						subheader={
							<Typography>
								{data.symbol} {price}
							</Typography>
						}
					/>
					<Alert
						type="success"
						showIcon
						message={
							<Typography>
								<Trans>Get rebates up to</Trans>{" "}
								<Typography.Text strong>
									{(data.rebate || {}).percent} (worth {data.symbol} {pricer((data.rebate || {}).amount)})
								</Typography.Text>{" "}
								<Trans>when you purchase this item</Trans>
							</Typography>
						}
					/>
					<Heading
						reverse
						content={<Trans>Available at</Trans>}
						subheader={
							<Typography>
								<Avatar src={listing.logoUrl} size={mobile ? "large" : "small"} />
								&nbsp; {listing.name}
							</Typography>
						}
					/>
					<Heading reverse content={<Trans>Description</Trans>} subheader={data.description} marginBottom="4em" />
					<a href={data.itemLink} target="_blank" rel="noopener noreferrer">
						<Button type="primary" className="mb2em">
							<Trans>
								Buy now <Icon type="right" />
							</Trans>
						</Button>
					</a>
					<Typography.Paragraph type="secondary" className="fs-09em">
						<Trans>
							Reminder: To ensure your order is tracked successfully and for your rebate to be processed, you need to make
							sure that you make the purchase from your web browser from this link (Buy Now button) only
						</Trans>
					</Typography.Paragraph>
				</Col>
			</Row>
		</Modal>
	)
}
